	module.exports = {"data":{"markdownRemark":{"html":"<p>Voyons ce qu'en dit la Parole de Dieu dans <strong>1 Corinthiens 12:4,11</strong> :</p>\n<blockquote>\n<p>« Il y a diversité de dons, mais le même Esprit; Un seul et même Esprit opère toutes ces choses, les distribuant à chacun <strong>en particulier comme il veut.</strong> »</p>\n</blockquote>\n<p>Les dons spirituels sont distribués par le Saint-Esprit. Aucun pasteur, ancien ou dirigeant d'église ne peut décider d'accorder ou \"d'apprendre\" à un membre le don des langues ou celui de la sagesse ou autre. C'est l'Esprit Saint qui décide, selon la Bible, et qui accorde des dons \"à chacun en particulier comme il veut\".</p>","frontmatter":{"title":"Qui distribue les dons spirituels ?"}}},"pathContext":{"slug":"/qui-distribue-les-dons-spirituels/"}}

